$theme-color: #0288D1;
$text-color: #37474F;
$Helvetica: "Helvetica Neue", Helvetica, Arial, Verdana, sans-serif;
$Oswald: Oswald, sans-serif;
$Rubik: Rubik, sans-serif;

$content-width: 70rem;
$shadow-with-color: 0 .2rem .25rem 0rem;
$shadow: 0 .2rem .25rem 0rem rgba(0,0,0,.4);

@mixin body-width {
	width: $content-width;
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	box-sizing: border-box;
}

@mixin centering-flex ($direction: column) {
	display: flex;
	flex-direction: $direction;
	justify-content: center;
}

$mobile: "only screen and (max-width: 767px)";

@function css-min($args...) {
	@return m#{i}n($args);
};

@function css-max($args...) {
	@return m#{a}x($args);
};

@function str-replace($string, $search, $replace: '') {
	$index: str-index($string, $search);
	@if $index {
		@return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
	}
	@return $string;
}

%focusable {
	box-sizing: border-box;
	border: 0;
	border-bottom: 3px solid #DDD;
	outline: 0;
	transition: border-bottom-color .2s ease-in-out;

	&:not([readonly]):hover,
	&:not([readonly]):focus,
	&:not([readonly]):focus-within {
		border-bottom-color: $theme-color;
	}

	&:not([readonly]):invalid:focus,
	&:not([readonly])[aria-invalid]:not([aria-invalid="false"]):focus,
	&:not([readonly]).invalid:focus,
	&:not([readonly]):invalid:focus-within,
	&:not([readonly])[aria-invalid]:not([aria-invalid="false"]):focus-within,
	&:not([readonly]).invalid:focus-within {
		border-bottom-color: change-color($theme-color, $hue: 60deg) !important;
	}

	&:not([readonly]):invalid,
	&:not([readonly])[aria-invalid]:not([aria-invalid="false"]),
	&:not([readonly]).invalid {
		border-bottom-color: change-color($theme-color, $hue: 0deg) !important;
	}
}

#flex {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	position: relative;

	> div:nth-child(2) {
		flex-basis: 45%;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}

	> div:nth-child(3) {
		flex-basis: 55%;
	}
}

#goback {
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	padding: .3rem 0;

	a {
		font-family: $Rubik;
		font-weight: 300;
		color: $theme-color;
		font-size: 1.2rem;
		padding: .3rem .7em;
		text-decoration: none;

		svg {
			position: relative;
			left: 0;
			transition: left .2s ease-in-out;
			stroke-width: .2px;
			fill: none;
			stroke: currentColor;
			display: inline-block;
			height: .8em;
			vertical-align: middle;
		}

		&:hover,
		&:focus {
			svg {
				left: -.2em;
			}

			span {
				text-decoration: underline;
			}
		}
	}
}

#icon svg {
	display: block;
	margin: 1em auto;
	width: css-min(20em, calc(100% - 10rem));

	&#s202 {
		stroke: transparentize($theme-color, .5);
		stroke-width: 10px;
		fill: none;

		polyline {
			stroke-linejoin: round;
			stroke-linecap: round;
		}
	}

	&#s500 {
		#s-triangle {
			fill: none;
			stroke: transparentize($theme-color, .5);
			stroke-width: 10px;
			stroke-linejoin: round;
		}

		#s-exclamation {
			fill: transparentize($theme-color, .5);
		}
	}

	&#s404 {
		#s-file {
			fill: transparentize($theme-color, .5);
		}

		#s-mouth {
			fill: none;
			stroke: transparentize($theme-color, .5);
			stroke-width: 8px;
			stroke-linecap: round;
		}

		.s-eye {
			fill: transparentize($theme-color, .5);
		}
	}
}

h1 {
	font-family: $Oswald;
	text-align: center;
	font-size: clamp(10em, 15em, 50vw);
	margin: -.1em 0 0;
	color: transparentize($theme-color, 0);
	line-height: 1;
	font-weight: normal;
	font-weight: 550;
}

h2 {
	margin: 0;
	font-family: $Rubik;
	font-weight: 300;
	text-align: center;
	padding: 0 1.5rem;
}